import { LogItem } from '../classes/logitem';

export class Log {
    private logItems : LogItem[];

    constructor(){
        this.logItems = [];
    }

    public getLogItems() : LogItem[]{
        return this.logItems;
    }

    public addLogItem(pLogItem : LogItem){
        this.logItems.push(pLogItem);
    }
}



