import { Module } from '../classes/module';
import { Artifact } from '../classes/artifact';

declare var require : any;
var _ = require('lodash');

export class ModuleManager {
    private selectedModule : Module;
    private modules : Module[];
    private moduleIdCounter : number;

    constructor(){
        this.modules = [];
        this.moduleIdCounter = 1;
    }

    public addModule(pModule : Module){
        this.modules.push(pModule);
    }

    public getModules() : Module[]{
        return this.modules;
    }

    public getModuleById(id: string){
        return _.find(this.modules, function(m) { return m.getId() == id });
    }

    public deleteModuleById(id: string){
        _.remove(this.modules, function(m) { return m.getId()  == id });
    }

    public getUniqueModuleName(name){
        if(this.checkModuleNameInUse(name)){
            var nameFound = false;
            var i=1;
            while(!nameFound){
                if(!this.checkModuleNameInUse(name + i)){
                    nameFound = true;
                    return name + i;
                }

                i++;
            }
        }

        return name;
    }

    public getNewModuleId(){
        return "m"+this.moduleIdCounter++;
    }

    public checkModuleNameInUse(name){
        for(let m of this.getModules()){
            if(m.getName() == name)
                return true;
        }

        return false;
    }

    //Replaces all unwanted symbols in the string and only leaves numbers and letters
    transformModuleName(name : String){
        return name.replace(/\s/g, "").replace(/\W/g, "");;
    }

    //Checks wether the given artifact or any of its children is already part of a module
    public artifactAlreadyInModule = (artifact : Artifact, modules : Module[], rootArtifact: Artifact) => {
        //Check for all the modules
        for(let m of modules){
            //Check for all the artifacts in the module
            for(let a of m.getArtifacts()){
                var check = this.artifactContains(artifact, a, rootArtifact);

                //The artifact is already part of a module (m)
                if(check === true){
                    return true;
                }else if(check == "root"){
                    return;
                }
            }
        }

        //Check the same for all the children of the artifact
        for(let a of artifact.artifacts){
            if(this.artifactAlreadyInModule(a , modules, rootArtifact)){
                return true;
            }
        }
    }

    //Checks wether the given artifact is part of the artifacts attributes
    //or if it is part of any of the children in the artifacts array
    private artifactContains = (artifact : Artifact, hostArtifact : Artifact, rootArtifact : Artifact) => {
        if(hostArtifact !== undefined && artifact !== undefined){
            //Check if we are trying to move the rootObject we declared beforehand
            if(artifact.id === hostArtifact.id && artifact.id === rootArtifact.id){
                return "root";
            }

            //If the artifact we look for has the same id as the currently looked at hostArtifact
            //we have found our module and return true
            if(artifact.id === hostArtifact.id){
                return true;
            }else{
                //Otherwise we look at all the children of the hostArtifact
                for(let ha of hostArtifact.artifacts){
                    if(this.artifactContains(artifact, ha, rootArtifact)){
                        return true;
                    }
                }
            }
        }

        return false;
    }
}



