import { Connection } from '../classes/connection';
import { ConnectionType } from '../classes/connectionType';

declare var require : any;
var _ = require('lodash');

export class ConnectionManager {
    private selectedConnection : Connection;
    private connections : Connection[];

    constructor(){
        this.connections = [];
    }

    public addConnection(pConnection : Connection){
        this.connections.push(pConnection);
    }

    public getConnections() : Connection[]{
        return this.connections;
    }

    public getSelectedConnection(){
        return this.selectedConnection;
    }

    public setSelectedConnection(pConnection: Connection){
        return this.selectedConnection = pConnection;
    }

    public getConnectionById(id: string){
        return _.find(this.connections, function(c) { return c.getJsPlumbConnection().id == id });
    }

    public deleteConnectionById(id: string){
        _.remove(this.connections, function(c) { return c.getJsPlumbConnection().id == id });
    }

    public changeTypeOfSelectedConnection(selectedType:string){
        switch(selectedType){
            case "expect": this.getSelectedConnection().setType(ConnectionType.EXPECT);break;
            case "forbid": this.getSelectedConnection().setType(ConnectionType.FORBID);break;
            default : this.getSelectedConnection().setType(ConnectionType.NULL);break;
        }
    }
}



