import { ArtifactComponent } from '../components/artifact.component'

export class Artifact {
    public name : string;
    public odname : string;
    public fullName : string;
    public type : string;
    public id : string;
    public artifacts : Artifact[];
    marked : boolean = false;
    //private artifactComponent : ArtifactComponent;

    constructor(pName:string,pODName:string,pFullName:string,pType:string, pId:string){
      this.name = pName;
      this.odname = pODName;
      this.fullName = pFullName;
      this.type = pType;
      this.id = pId;
      this.artifacts = [];
    }

    addArtifact(pArtifact : Artifact){
        this.artifacts.push(pArtifact);
    }

    public markArtifact(){
        this.marked = true;

        for (let a of this.artifacts) {
            a.markArtifact();
        }
    }

    public unmarkArtifact(){
        this.marked = false;

        for (let a of this.artifacts) {
            a.unmarkArtifact();
        }
    }

    public getArtifacts(){
        return this.artifacts;
    }

    public getName(){
        return this.name;
    }

    public getODName(){
        return this.odname;
    }

    public getFullName(){
        return this.fullName;
    }

    public getId(){
        return this.id;
    }



    /*
    setArtifactComponent(pArtifactComponent : ArtifactComponent){
        this.artifactComponent = pArtifactComponent;
    }
    */
}



