import { Artifact } from '../classes/artifact';

export class ArtifactManager {
    private artifacts : Artifact[];
    private artifactsFlat : Artifact[];

    constructor(){
        this.artifacts = [];
        this.artifactsFlat = [];
    }

    public addArtifact(pArtifact : Artifact){
        this.artifacts.push(pArtifact);
    }

    public getArtifacts() : Artifact[]{
        return this.artifacts;
    }

    public getArtifactsFlat() : Artifact[]{
        return this.artifactsFlat;
    }
}



