import { Artifact } from '../classes/artifact';

export class Module {
    private name : string;
    private id : string;
    private artifacts : Artifact[];
    private expanded : boolean;

    constructor(pName:string,pId:string){
      this.name = pName;
      this.id = pId;
      this.artifacts = [];
      this.expanded = true;
    }

    public addArtifact(pArtifact : Artifact){
        this.artifacts.push(pArtifact);
    }

    public getArtifacts(){
        return this.artifacts;
    }

    public getName(){
        return this.name;
    }

    public getId(){
        return this.id;
    }

    public setName(newName:string){
        this.name = newName;
    }
}



