import { NgModule, ModuleWithProviders } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';
import { DragulaModule } from 'ng2-dragula';

import { ModulesRoutingModule } from './modules-routing.module';
import { ModulesComponent } from './modules/modules.component';
import { ModuleComponent } from './components/module.component'
import { ArtifactComponent } from './components/artifact.component';

import { ModulesService } from './modules.service';
import { SelectedProjectService } from "../selected-project.service";
import { SelectedOdService } from "../selected-od.service";

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ModulesRoutingModule,
    DragulaModule
  ],
  exports: [

  ],
  declarations: [
      ModulesComponent,
      ModuleComponent,
      ArtifactComponent
  ],
  providers : [
      ModulesService
  ]
})
export class ModulesModule {
  static forRoot(): ModuleWithProviders {
    return {
      ngModule: ModulesModule,
      providers: [ModulesService,SelectedProjectService,SelectedOdService]
    };
  }
}



