import { Component, Input, ChangeDetectionStrategy, ChangeDetectorRef, ViewChildren } from '@angular/core';
import { Artifact } from '../classes/artifact'
import { Module } from '../classes/module'
import { ModulesComponent } from '../modules/modules.component'

@Component({
  selector: 'artifact-component',
  templateUrl: './artifact.component.html',
  styleUrls: ['./artifact.component.css'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class ArtifactComponent {
    @Input() artifact : Artifact;
    @Input() module : Module;
    @Input() parent : ModulesComponent;
    @ViewChildren(ArtifactComponent) artifactComponents;

    private ref : ChangeDetectorRef;
    expanded : boolean;

    constructor(pRef: ChangeDetectorRef) {
        this.expanded = false;
        this.ref = pRef;
    }

    public updateArtifactComponent(){
        this.ref.detectChanges();

        for (let a of this.artifactComponents._results) {
            a.updateArtifactComponent();
        }

    }

    private toggleExpanded(){
        this.expanded = !this.expanded;

        if(this.parent !== undefined)
            this.parent.recalculateModuleOffsets();
    }

    ngOnInit() {
        //this.setArtifactComponentForChildren();
        this.ref.detectChanges();
    }

    /*
    setArtifactComponentForChildren(){
        if(this.artifacts !== undefined){
            for (let a of this.artifacts) {
                a.setArtifactComponent(this);
            }
        }
    }
    */
}



