export class LogItem {
    private type : string;
    private message : string;

    constructor(pType:string,pMessage:string){
        this.type = pType;
        this.message = pMessage;
    }

    public getType(){
        return this.type;
    }

    public getMessage(){
        return this.message;
    }
}



