import { ConnectionType } from '../classes/connectionType';
import { Reference } from '../classes/reference';

export class Connection {
    private jsPlumbConnection: any;
    private source: string;
    private target: string;
    private type: ConnectionType;
    private references : Reference[];

    constructor(pJsPlumbConnection: any,pSource:string,pTarget:string,pType:ConnectionType){
        this.jsPlumbConnection = pJsPlumbConnection;
        this.source = pSource;
        this.target = pTarget;
        this.type = pType;
        this.references = [];
    }

    public getJsPlumbConnection(){
        return this.jsPlumbConnection;
    }

    public getSource(){
        return this.source;
    }

    public getTarget(){
        return this.target;
    }

    public getType(){
        return this.type;
    }

    public setSource(pSource : string){
        this.source = pSource;
    }

    public setTarget(pTarget : string){
        this.target = pTarget;
    }

    public setJsPlumbConnection(pConnection: Connection){
        this.jsPlumbConnection = pConnection;
    }

    public setType(pType: ConnectionType){
        this.type = pType;
    }

    public getReferences(){
        return this.references;
    }

    public addReference(pReference:Reference){
        this.references.push(pReference);
    }
}



