import { Module } from '../classes/module';
import { Artifact } from '../classes/artifact';

export class Reference {
    private sourceModule : Module;
    private targetModule : Module;
    private sourceArtifact : Artifact;
    private targetArtifact : Artifact;
    private referenceType : string;
    private count : number;

    constructor(pSourceModule : Module,pTargetModule : Module,pSourceArtifact : Artifact,pTargetArtifact : Artifact,pReferenceType : string,pCount : number){
        this.sourceModule = pSourceModule;
        this.targetModule = pTargetModule;
        this.sourceArtifact = pSourceArtifact;
        this.targetArtifact = pTargetArtifact;
        this.referenceType = pReferenceType;
        this.count = pCount;
    }

    public getSourceModule(){
        return this.sourceModule;
    }

    public getTargetModule(){
        return this.targetModule;
    }

    public getSourceArtifact(){
        return this.sourceArtifact;
    }

    public getTargetArtifact(){
        return this.targetArtifact;
    }

    public getReferenceType(){
        return this.referenceType;
    }

    public getCount(){
        return this.count;
    }
}



