import { Injectable } from '@angular/core';
import { BehaviorSubject, Subject, Observable } from "rxjs";
import { Http , Response, Headers, RequestOptions } from "@angular/http";

@Injectable()
export class ModulesService {
    constructor(private http:Http){

    }

    //Make a post request to look for the available analysis files
    getAnalysis(payload): Observable<JSON>{
        return this.http.post('/getModuleAnalysis.php', payload ).map((res: Response) => res.json()).catch (error => <any>error);
    }

    //Make a post request to the OD Generator
    generateOD(payload): Observable<JSON>{
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ headers: headers });
        //return this.http.post('/generateODForModules.php', payload, options ).map((res: Response) => res.json()).catch (error => <any>error);
        return this.http.post('/generateODForModules.php', payload, options ).map((res: Response) => res.json()).catch (error => <any>error);
    }
}



